
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'CheckboxComponent',
  props: {
    label: String,
    value: Boolean,
  },
  setup(props, { emit }) {
    const toggleCheckboxValue = () => {
      emit('update:value', !props.value);
    };

    return {
      toggleCheckboxValue,
    };
  },
});
