const _hoisted_2 = /*#__PURE__*/_createStaticVNode("<h1 class=\"page-title\">Abstraction in TypeScript</h1><div class=\"code-example-block\"><div class=\"code-example__title\">Abstract Class</div><div class=\"code-example__description\"> A TypeScript Abstract class is a class that may have some unimplemented methods. These methods are called abstract methods. <br> We can&#39;t create an instance of an abstract class. But other classes can derived from abstract class and reuse the functionality of base class. <br> To declare an abstract class, we need to use the <b>abstract</b> keyword. <br> We must declare an abstract method in the child class. <br> We can&#39;t declare an abstract method in the not-abstract class. <br> The most common use of abstract classes in TypeScript is to locate some common behavior to share within related subclasses. </div><div class=\"code-example\"><pre>          <code class=\"language-javascript\">\n            import { onMounted } from &#39;vue&#39;;\n\n            abstract class BaseClass {\n              firstName: string;\n\n              lastName: string;\n\n              age: number;\n\n              protected constructor(firstName: string, lastName: string, age: number) {\n                this.firstName = firstName;\n                this.lastName = lastName;\n                this.age = age;\n              }\n\n              getName(): string {\n                return `${this.firstName} ${this.lastName}`;\n              }\n\n              getAge(): number {\n                return this.age;\n              }\n\n              abstract getEmployedStatus(): boolean;\n            }\n\n            class ChildClass extends BaseClass {\n              isEmployed: boolean;\n\n              constructor(firstName: string, lastName: string, age: number, isEmployed: boolean) {\n                super(firstName, lastName, age);\n                this.isEmployed = isEmployed;\n              }\n\n              getEmployedStatus(): boolean {\n                return this.isEmployed;\n              }\n            }\n\n            onMounted(() =&gt; {\n              console.log(&#39;Abstraction in TypeScript&#39;);\n              const NewEmployee = new ChildClass(&#39;John&#39;, &#39;Wick&#39;, 36, true);\n\n              console.log(NewEmployee.getName());\n              console.log(NewEmployee.getAge());\n              console.log(NewEmployee.getEmployedStatus());\n            });\n          </code>\n        </pre></div><a href=\"https://github.com/YuriyShandor/code-examples/blob/vue3-with-typescript/src/components/typescript/TypeScriptAbstraction.vue\" target=\"_blank\" class=\"button code-example__button\"> Watch on GitHub <img src=\"/images/github-logo.svg\" alt=\"\" class=\"code-example__button-image\"></a></div>", 2)
