
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'SuperHeroDetailsModal',
  props: {
    superHero: Object,
  },
  setup(props, { emit }) {
    const setSelectedSuperHero = () => {
      emit('set-selected-superhero', undefined);
    };

    return {
      setSelectedSuperHero,
    };
  },
});
