
import { defineComponent, ref } from 'vue';

export default defineComponent({
  name: 'SuperHeroSearch',
  setup(props, { emit }) {
    const searchField = ref('');

    const findSuperHero = () => {
      emit('update-superhero-list', searchField.value);
    };

    return {
      searchField,
      findSuperHero,
    };
  },
});
