
import { defineComponent, reactive, watch, onMounted } from 'vue';
import { ElOption, ElSelect } from 'element-plus';

export default defineComponent({
  name: 'SelectComponent',
  props: {
    label: String,
    id: String,
    value: [String, Number],
    isRequired: Boolean,
    selectOptions: Array,
    isFilterable: Boolean,
  },
  components: {
    ElSelect,
    ElOption,
  },
  setup(props, { emit }) {
    const state = reactive({
      selectValue: '' as (string | number),
      isDirty: false as boolean,
      isValid: false as boolean,
      errors: [] as Array<string>,
    });

    const validateField = () => {
      state.errors = [];
      if (props.isRequired && state.selectValue === '') {
        state.errors.push('Please select an option from the dropdown menu. ');
      }
      state.isValid = state.errors.length === 0;
    };

    onMounted(() => {
      if (props.value !== undefined && props.value !== '') {
        state.selectValue = props.value;
        document.getElementById(props.id)?.focus();
      }
    });

    watch(() => state.selectValue, () => {
      if (!state.isDirty) {
        state.isDirty = true;
      }
      validateField();
      if (state.isDirty && !state.isValid) {
        emit('update:value', '');
      } else {
        emit('update:value', state.selectValue);
      }
    });

    return {
      state,
    };
  },
});
